import React, {Fragment} from 'react';
import Link from 'gatsby-link';

import CSS from '../css/app.module.css';

export default () => {
	return (
		<Fragment>
			<nav>
				<Link to="/">Home</Link>
				<Link to="/about">About</Link>
			</nav>
			<div className={CSS.wrap}>Hello world from Home!</div>
		</Fragment>
	);
};



